/* Существующие стили */
.app-container {
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.content-container {
  flex: 1;
  overflow-y: auto;
}

.main-content {
  padding: 20px;
}

.header-image {
  text-align: center;
  margin-bottom: 20px;
}

.header-title {
  font-size: 24px;
  color: #333;
}

.sleep-content {
  padding: 10px;
}

.section-title {
  font-size: 20px;
  margin-bottom: 10px;
  color: #333;
}

.card-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 15px;
  margin-bottom: 20px;
}

.story-card {
  background-size: cover;
  border-radius: 10px;
  padding: 15px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 150px;
  position: relative;
}

.story-info {
  color: white;
  z-index: 1;
}

.story-title {
  font-size: 16px;
  margin: 0;
}

.story-author {
  font-size: 12px;
  margin: 5px 0 0;
}

.play-icon {
  position: absolute;
  bottom: 10px;
  right: 10px;
  background: rgba(255, 255, 255, 0.8);
  border-radius: 50%;
  padding: 5px;
  border: none;
  cursor: pointer;
}

.play-icon svg {
  color: #333;
}

/* Новый контейнер для кнопок */
.button-container {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 20px;
}

.sleep-button {
  padding: 10px 20px;
  background-color: #F5C563;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

.sleep-button:hover {
  background-color: #E5989B;
}

.subscription-info {
  font-size: 14px;
  color: #333;
  text-align: center;
}

/* Модальные окна и плеер остаются без изменений */